import {Component, Directive, ElementRef, Input, OnInit} from '@angular/core'

import {BackendServices} from '../services/backend.service';
import {Node} from '../domain/node';
import {Edge} from '../domain/edge';

import * as d3 from 'd3';


@Directive({
    selector: "[d3PkgDep]"
})
export class D3PkgDepDirective {

    rNodes = Array<Node>(); // requirererNodes
    pNodes = Array<Node>(); // providerNodes

    constructor(private _el: ElementRef, private _backend: BackendServices) { }

    ngOnInit() {
        var url = "http://localhost:2002/backend/v1/snapshotdetails/latest"
        d3.json(url, function (error, data) {
            if (error != null) {
                console.log(error);
            }

            var nodes = Array<Node>();
            var edges = Array<Edge>();
            var toCounter = new Map<string, number>();

            var filteredBundles = data.bundles.filter(function(el) {
                console.log(el.id);
                return el.id == 0 ? null : el;
            });


            filteredBundles.forEach(bundle => {
                //console.log("creating new Node with id " + bundle.id);
                nodes.push(new Node(bundle.id, bundle.symbolicName, 17, 500));
                //console.log(bundle.wireDescriptor.providedWires);
                var arr = Object.keys(bundle.wireDescriptor.providedWires).map(function (key) {
                    return {
                        key: key,
                        value: bundle.wireDescriptor.providedWires[key]
                    }
                });
                arr.forEach(wire => {
                    //console.log(wire);
                    edges.push(new Edge(bundle.id, wire.key, wire.value));
                });
            });

            var nodeHash = {};
            for (let x in nodes) {
                nodeHash[nodes[x].id] = nodes[x];
            };
            //console.log(nodeHash);
            for (let x in edges) {
                edges[x].weight = parseInt(edges[x].weight);
                edges[x].source = nodeHash[edges[x].source];
                edges[x].target = nodeHash[edges[x].target];
            };
            //console.log(edges);

            var weightScale = d3.scale.linear()
                .domain(d3.extent(edges, function (d) { return d.weight; }))
                .range([.3, 3]);

            var force = d3.layout.force().charge(-2000)
                .size([1000, 600])
                .nodes(nodes)
                .links(edges)
                .on("tick", forceTick);

            d3.select("#d3PkgDep").selectAll("line.link")
                .data(edges, function (d) { return d.source.id + "-" + d.target.id; })
                .enter()
                .append("line")
                .attr("class", "link")
                .style("stroke", "black")
                .style("opacity", .5)
                .style("stroke-width", function (d) { return weightScale(d.weight) });

            var nodeEnter = d3.select("#d3PkgDep").selectAll("g.node")
                .data(nodes, function (d) { return d.id })
                .enter()
                .append("g")
                .attr("class", "node");

            nodeEnter.append("circle")
                .attr("r", function (d) { return 4; })
                .style("fill", "lightgray")
                .style("stroke", "black")
                .style("stroke-width", "1px");

            nodeEnter.append("text")
                .style("text-anchor", "middle")
                .attr("x", 18)
                .attr("y", 18)
                .text(function (d) { return d.id; });

            force.start();

            var marker = d3.select("#d3PkgDep").append('defs')
                .append('marker')
                .attr("id", "Triangle")
                .attr("refX", 12)
                .attr("refY", 6)
                .attr("markerUnits", 'userSpaceOnUse')
                .attr("markerWidth", 12)
                .attr("markerHeight", 18)
                .attr("orient", 'auto')
                .append('path')
                .attr("d", 'M 0 0 12 6 0 12 3 6');
            d3.selectAll("line").attr("marker-end", "url(#Triangle)");
            
            function forceTick() {
                d3.selectAll("line.link")
                    .attr("x1", function (d) { return d.source.x; })
                    .attr("x2", function (d) { return d.target.x; })
                    .attr("y1", function (d) { return d.source.y; })
                    .attr("y2", function (d) { return d.target.y; });

                d3.selectAll("g.node")
                    .attr("transform", function (d) {
                        return "translate(" + d.x + "," + d.y + ")";
                    })
            }
        });
    }


}